import React, { memo } from 'react'

import { FooterWrapper,FooterLeft,FooterRight } from './style'

import { footerImages } from '@/common/local-data'

const AppFooter = memo(() => {
  return (
    <FooterWrapper>
        <div className='content wrap-v2'>
            <FooterLeft>
                <div className='guide'>
                    <a href='https://st.music.163.com/official-terms/service' rel="noopener noreferrer" target="_blank"></a>
                    <span>|</span>
                    <a href='https://st.music.163.com/official-terms/service' rel="noopener noreferrer" target="_blank"></a>
                    <span>|</span>
                    <a href='https://st.music.163.com/official-terms/service' rel="noopener noreferrer" target="_blank"></a>
                    <span>|</span>
                    <a href='https://st.music.163.com/official-terms/service' rel="noopener noreferrer" target="_blank"></a>
                </div>
                <div className='company-info'>
                    <div>1997-2022 </div>
                    <div></div>
                </div>
                <div className='record'>B2-20090191-18  </div>
            </FooterLeft>
            <FooterRight>
            {
                footerImages.map((item, index) => {
                    return (
                    <li className="item" key={item.link}>
                        <a className="link" href={item.link} rel="noopener noreferrer" target="_blank"> </a>
                        <span className="title">{item.title}</span>
                    </li>
                    )
                })
            }
            </FooterRight>
        </div>
    </FooterWrapper>
  )
})

export default AppFooter